export default {
	components: {
	},
	data() {
		return {
			list: [],
			loadtext: "上拉加载更多",
			emit: 10,
			dev_id: ""
		}
	},
	onLoad() {
		this.__init()
	},
	onReachBottom() {
		if(this.emit > this.list.length) {
			console.log('不会再上拉了哦');
			return
		}
		console.log('啦啦啦');
		this.loadtext = "加载中..."
		this.emit += 10 
		console.log("触发上拉加载", this.emit);
		this.__init()
	},
	methods: {
		select(item) {
			try {
				console.log('item:', item);
			    uni.setStorageSync('dev_class', item);
				uni.setStorageSync('dev_name', '单个');
				uni.navigateTo({
					url: '/pages/device/device'
				})
			} catch (e) {
			    // error
				console.log('catch error!!', e);
			}
		},
		searchbtn() {
			this.__init()
		},
		async __init(callback = false) {
			this.$H.post("/agent/", {
				user_id: uni.getStorageSync('uid'),
				token: uni.getStorageSync('utoken'),
				opt: "device_list",
				slimit: 0,
				elimit: this.emit,
				device_id: this.dev_id
			}).then((data) => {
				this.list = data.arr
				console.log('this.list.length:', this.list.length);
				console.log('this.emit.length:', this.emit);
				// 恢复加载状态
				this.loadtext = this.list.length < this.emit ? "没有更多了" :  "上拉加载更多"
			}).catch(() => {
				console.log("catch error");
			})
		}
	}
	
	
}
